body {
  letter-spacing: .5px;
  background-color: rgba(182, 134, 201, .992);
  margin: 16px;
  font-family: Franklin Gothic Medium;
  line-height: 1.5;
}

#search-box {
  max-width: 320px;
  font: inherit;
